<template>
  <div class="forget">
    <van-nav-bar title="忘记密码" left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-cell @click="showPopup" title="邮箱验证" size="large" is-link />

    <!-- 请输入绑定邮箱 -->
    <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
      <van-form @submit="saveEmail">
        <van-field
          v-model="email"
          type="email"
          name="邮箱"
          label="邮箱"
          placeholder="邮箱"
          autocomplete="false"
          :rules="[{pattern:emailPattern, required: true, message: '请填写正确邮箱号' }]"
        />
        <van-field
          v-model="emailCode"
          center
          clearable
          label="邮箱验证码"
          placeholder="请输入邮箱验证码"
          :rules="[{pattern:ValidCodePattern, required: true, message: '验证码格式错误' }]"
        >
          <template #button>
            <van-button
              :disabled="isSend"
              @click="sendCode"
              native-type="button"
              type="primary"
              size="small"
              color="#7f5839"
            >{{text}}</van-button>
          </template>
        </van-field>
        <div style="margin: 16px;">
          <van-button round block type="info" color="#7f5839" native-type="submit">下一步</van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>

<script>
import Api from "../api/api";
import Util from "../util/utils";
export default {
  data() {
    return {
      email: "",
      show: false,
      emailPattern: /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
      emailCode: "",
      isSend: false,
      ValidCodePattern: /^[0-9]{6}$/,
      text: "发送验证码"
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    showPopup() {
      this.show = true;
    },
    sendCode() {
      if (!this.emailPattern.test(this.email)) {
        this.$toast("请先输入正确的邮箱");
        return;
      }

      let time = 60;
      this.isSend = true;
      this.text = `${time}s后重新发送`;
      // 发送验证码
      let timer = window.setInterval(() => {
        if (time == 0) {
          this.text = "发送验证码";
          this.isSend = false;
          window.clearInterval(timer);
          return;
        } else {
          this.text = `${--time}s后重新发送`;
        }
      }, 1000);

      // 发请求
      Util.showLoading(this, "正在发送中");

      Api.emailValidCode({ email: this.email }).then(res => {
        this.$toast(res.data.msg);
      });
    },
    saveEmail() {
      // 发请求
      Util.showLoading(this, "正在验证");

      Api.checkValidCode({ validCode: this.emailCode }).then(res => {
        if (res.data.code == "K001") {
          // 成功跳转
          this.$toast(res.data.msg);
          this.$router.push({ name: "Retrieve" });
        }

        this.$toast(res.data.msg);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.forget {
  height: 100%;
  background-color: #eee;
}
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}

/deep/ .van-cell {
  margin: 20px 0;
}
</style>